import React from 'react'

import Layout from '../components/layout'
import SEO from '../components/seo'
import Link from 'gatsby-link'
import {graphql} from "gatsby"

export default function Template({data}) {
    const {LessonsQuery: lessons} = data
    const {CourseQuery: course} = data
    const pageTitle = course.frontmatter.title

    return (
        <Layout>
            <SEO title={pageTitle} />
            <div className="courses-header pb-5 pt-5">
                <div className="container">
                    <h1 className="mb-0">{pageTitle}</h1>
                </div>
            </div>
            <div className="container mb-5 mt-5">
                <h2>Lessons</h2>
                {lessons.edges.map(lesson => (
                    <div key={lesson.node.id} className="video-container mb-3">
                        <Link to={lesson.node.frontmatter.path}>
                            <h4>{lesson.node.frontmatter.title}</h4>
                        </Link>
                        <div className="row ml-0 timing">
                          <span className="material-icons mr-1">access_time</span>
                          <div className="">{lesson.node.frontmatter.time}</div>
                        </div>
                        <div className="video mt-2">
                            <Link to={lesson.node.frontmatter.path} className="btn btn-outline-secondary">Learn More</Link>
                        </div>
                    </div>
                ))}
            </div>
        </Layout>
    )
}

export const pageQuery = graphql`
  query($course: String!) {
    LessonsQuery: allMarkdownRemark(
      filter: { 
        frontmatter: {
          course: {eq: $course},
          type: {eq: "lesson"},
          active: {eq: 1}
        }
      },
      sort: {
        fields: [frontmatter___order],
        order: ASC
      }
    ) {
      edges {
        node {
          id
          frontmatter {
            title
            path
            time
          }
        }
      }
    }
    
    CourseQuery: markdownRemark(
      frontmatter: {
        code: {
          eq: $course 
        }
      }
    ) {
      id
      frontmatter {
        title
      }
    }
  }
`
